import {
    Col,
    Row,
    Button,
    Form,
    Input,
    DatePicker,
    Modal,
    Table,
    Space,
    Avatar,
    Select,
    Checkbox,
    InputNumber,
    Upload,
    Collapse,
    Divider
} from "antd";
import * as event from "../../event";
import React from 'react'
import {Link} from "react-router-dom";
import {InboxOutlined, PlusOutlined} from '@ant-design/icons'
import * as util from '../../util'
import test_img from '../../imgs/7777.png'

export const OCR = (props) => {
    const [imgs, setImags] = React.useState({
        previewVisible: false,
        previewImage: '',
        previewTitle: '',
        fileList: [],
        panelKey: [],
        viewResult: false
    })
    const uploadButton = (
        <div>
            <PlusOutlined/>
            <div style={{marginTop: 8}}></div>
        </div>
    );

    function getBase64(file, callback) {
        const reader = new FileReader();
        reader.addEventListener('load', () => callback(reader.result));
        reader.readAsDataURL(file);
    }

    const columns = [
        {
            title: '',
            dataIndex: 'id',
            key: 'id'
        }, {
            title: '',
            dataIndex: 'status',
            key: 'status',
            render: (text, record) => {
                if (record.status === 1)
                    return ""
                else {
                    return ""
                }
            }
        }, {
            title: '',
            key: 'action',
            render: (text, record) => {
                return (
                    <>
                        <Button type="link" disabled={record.status ? false : true}
                                onClick={(e) => setImags({...imgs, ...{viewResult: true}})}></Button>
                        <Button type="link"></Button>
                        <Modal
                            visible={imgs.viewResult}
                            title={""}
                            footer={null}
                            onCancel={() => setImags({...imgs, ...{viewResult: false}})}
                            width={1000}
                        >
                            <Space direction={"vertical"}>
                                <img style={{width: '100%'}}
                                     src={test_img}/>
                                <Divider type={"horizontal"}/>
                                <div>
                                    20121Google2016AlphaGo
                                    2017 AlphaZeroAlphaGoDeepMindAI
                                    
                                    
                                    
                                    
                                </div>
                            </Space>


                        </Modal>
                    </>
                )
            }
        }
    ]
    return (
        <>
            <Collapse onChange={(d) => {
                setImags({...imgs, ...{panelKey: d}})
            }} activeKey={imgs.panelKey} style={{margin: "10px 0px"}}>
                <Collapse.Panel collapsible={'disabled'} showArrow={false} header="" key={'1'}>
                    <Upload
                        listType="picture-card"
                        fileList={imgs.fileList}
                        multiple={true}
                        onPreview={file => {
                            util.log(file)
                            setImags({...imgs, ...{previewVisible: true, previewImage: file.url}})
                        }}
                        beforeUpload={file => {
                            getBase64(file, fileObj => {
                                let t_imgs = {...imgs}
                                t_imgs.fileList.push({
                                    uid: t_imgs.fileList.length,
                                    name: 'image.png',
                                    status: 'done',
                                    url: fileObj,
                                })
                                setImags(t_imgs)
                            })
                        }}
                        onRemove={(file) => {
                            setImags({...imgs, ...{fileList: imgs.fileList.filter(({uid}) => uid !== file.uid)}})
                        }}
                    >
                        {imgs.fileList.length >= 8 ? null : uploadButton}
                    </Upload>
                    <Button onClick={(e) => {
                        setImags({...imgs, ...{panelKey: [], fileList: []}})
                    }}></Button>
                    <Modal
                        visible={imgs.previewVisible}
                        title={""}
                        footer={null}
                        onCancel={() => setImags({...imgs, ...{previewVisible: false}})}
                    >
                        <img alt="example" style={{width: '100%'}} src={imgs.previewImage}/>
                    </Modal>
                </Collapse.Panel>
            </Collapse>
            <Table
                columns={columns}
                dataSource={[
                    {
                        id: "1605760010680",
                        status: 0
                    },
                    {
                        id: "1605760012650",
                        status: 1
                    },
                    {
                        id: "1605760020120",
                        status: 0
                    },
                    {
                        id: "1605760020360",
                        status: 1
                    },
                    {
                        id: "1605760030650",
                        status: 1
                    }
                ]}
                size={'small'}
                bordered
            />
        </>
    )
}
