import React from 'react'
import {store} from "../../store"
import * as event from '../../event'
import * as comp from '../content'
import * as g from '../../g'
import {
    Space,
    Button,
    Col,
    Row,
    Table,
    Modal,
    Form,
    Input,
    message,
    Radio,
    Select,
    InputNumber,
    Popconfirm, Tag
} from "antd";
import * as util from '../../util'


export const UserGroup = (props) => {
    const state = store.useContext()
    const [showModal, setShowModal] = React.useState(false)
    const [modalInit, setModalInit] = React.useState({})
    let menus = {}
    state.setting.menu.menu.map(item => {
        menus[item._id] = item
        return item
    })
    React.useEffect(() => {
        const getMenu = async () => {
            await Promise.all([
                event.menu.getMenuGroup(),
                event.menu.getMenu()
            ])
        }
        getMenu()
    }, [])

    const columns = [
        {
            title: '',
            dataIndex: 'name',
            key: 'name',
            fixed: 'left',
            ...util.getColSearchLocal('', 'name')
        }, {
            title: '',
            dataIndex: 'member',
            key: 'member',
            render: (text, record) => {
                if (record.type === g.menu_group_type.menu) {
                    return state.setting.menu.menu.filter((item) => record.member.indexOf(item._id) !== -1).map(item => {
                        return item.name
                    }).join(" | ")
                } else if (record.type === g.menu_group_type.menu_group) {
                    return state.setting.menu_group.menu_group.filter((item) => record.member.indexOf(item._id) !== -1).map(item => {
                        return item.name
                    }).join(" | ")
                }

            }
        }, {
            title: '',
            key: 'action',
            render: (text, record) => {
                return (
                    <Popconfirm title="?"
                                cancelText={""} okText={''}
                                onCancel={(e) => e.stopPropagation()}
                                onConfirm={async (e) => {
                                    e.stopPropagation()
                                    await event.menu.deleteMenuGroup(record._id)
                                }}>
                        <Button danger onClick={(e) => e.stopPropagation()}></Button>
                    </Popconfirm>
                )
            }
        }
    ]
    return (
        <>
            <Row justify="end">
                <Col span={2}>
                    <EditUserGroup show={showModal} setShow={setShowModal} initData={modalInit}
                                   setInitData={setModalInit}/>
                    <Button type={'primary'} onClick={async () => {
                        setModalInit({
                            _id: '',
                            name: '',
                            type: g.menu_group_type.menu,
                            member: []
                        })
                        setShowModal(true)
                    }}></Button>
                </Col>
            </Row>
            <Table
                columns={columns}
                dataSource={state.setting.menu_group.menu_group}
                size={'small'}
                scroll={{y: 750}}
                bordered
                onRow={(record) => {
                    return {
                        onClick: (event) => {
                            setModalInit({
                                _id: record._id,
                                name: record.name,
                                type: record.type,
                                member: record.member
                            })
                            setShowModal(true)
                        }
                    }
                }}
            />
        </>
    )
}


const EditUserGroup = (props) => {
    const state = store.useContext()
    const [form] = Form.useForm()
    form.setFieldsValue(props.initData)
    return (
        <>
            <Modal
                title={''}
                visible={props.show}
                okText={''} cancelText={''}
                onCancel={(e) => {
                    props.setShow(false)
                    form.resetFields()
                }}
                onOk={() => {
                    form.validateFields()
                        .then(async (values) => {
                            if (!await event.menu.createMenuGroup(values)) {
                                message.error('')
                                return
                            } else {
                                message.success('')
                                props.setShow(false)
                                form.resetFields()
                            }
                        })
                }}
            >
                <Form form={form}>
                    <Form.Item label={'_id'} name={'_id'} hidden={true}>
                        <Input/>
                    </Form.Item>
                    <Form.Item label={''} name={'name'} rules={[
                        {
                            required: true,
                            message: 'is required!',
                        }
                    ]}>
                        <Input/>
                    </Form.Item>
                    <Form.Item label={""} name={'type'} rules={[
                        {
                            required: true,
                            message: 'is required!',
                        }
                    ]}>
                        <Radio.Group onChange={(e) => {
                            let now_member = form.getFieldValue('type') === props.initData.type ? props.initData.member : []
                            form.setFieldsValue({member: now_member})
                        }}>
                            <Radio.Button value={g.menu_group_type.menu}></Radio.Button>
                            <Radio.Button value={g.menu_group_type.menu_group}></Radio.Button>
                        </Radio.Group>
                    </Form.Item>
                    {/* or */}
                    <Form.Item noStyle shouldUpdate>
                        {
                            (ins) => {
                                let menu_group_type = ins.getFieldValue('type')
                                let member = []
                                if (menu_group_type === g.menu_group_type.menu) {
                                    member = state.setting.menu.menu
                                } else if (menu_group_type === g.menu_group_type.menu_group) {
                                    member = state.setting.menu_group.menu_group
                                }
                                return (
                                    <Form.Item label={""} name={'member'}
                                               rules={[{required: true, message: 'is required!'}]}>
                                        <Select
                                            mode="multiple"
                                            allowClear
                                            placeholder=""
                                            optionFilterProp="children"
                                            filterOption={(input, option) =>
                                                option.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
                                            }
                                        >
                                            {member.filter(item => item.type === g.menu.MENU_ITEM).map((item) =>
                                                <Select.Option key={item._id}
                                                               value={item._id}>{item.name}</Select.Option>)}
                                        </Select>
                                    </Form.Item>
                                )
                            }
                        }
                    </Form.Item>
                </Form>
            </Modal>
        </>
    )
}
