import {store} from "../../store";
import moment from 'moment'
import {
    Col,
    Row,
    Button,
    Form,
    Input,
    message,
    Modal,
    Table,
    Popconfirm,
    Tag,
    DatePicker,
    Select,
    Space,
    Popover
} from "antd";
import * as event from "../../event";
import React from 'react'
import * as g from "../../g";
import * as util from '../../util'


export const SettingUser = (props) => {
    const state = store.useContext()
    const [showModal, setShowModal] = React.useState(false)
    const [modalInit, setModalInit] = React.useState({})
    const [pwdForm] = Form.useForm()
    React.useEffect(() => {
        (async () => {
            await Promise.all([
                event.menu.getMenuGroup(),
                event.user.getUser()
            ])
        })()
    }, [])
    const columns = [
        {
            title: '',
            dataIndex: 'username',
            key: 'username',
            ...util.getColSearchLocal('', 'name')
        }, {
            title: '',
            dataIndex: 'role',
            key: 'role',
            render: (text, record) => {
                return g.user_role_desc[record.role]
            }
        }, {
            title: '',
            dataIndex: 'group_list',
            key: 'group_list',
            render: (text, record) => {
                if (record.role === g.user_role.normal) {
                    return state.setting.menu_group.menu_group.filter((item) => record.group_list.indexOf(item._id) !== -1).map(item => {
                        return <Tag>{item.name}</Tag>
                    })
                } else {
                    return <Tag></Tag>
                }

            }
        }, {
            title: '',
            key: 'action',
            render: (text, record) => {
                if (record.role === g.user_role.normal) {
                    return (
                        <>
                            <Space>
                                <Popconfirm title="?"
                                            cancelText={""} okText={''}
                                            onCancel={(e) => e.stopPropagation()}
                                            onConfirm={async (e) => {
                                                e.stopPropagation()
                                                await event.user.deleteUser(record._id)
                                            }}>
                                    <Button danger onClick={async (e) => {
                                        e.stopPropagation()
                                        if (await event.user.deleteUser(record._id)) {
                                            message.success('')
                                        } else {
                                            message.error('')
                                        }
                                    }}></Button>
                                </Popconfirm>
                                <Popover
                                    content={() => {
                                        return <Form form={pwdForm}>
                                            <Form.Item name={"pwd"}>
                                                <Input type={'password'} onClick={(e) => e.stopPropagation()}
                                                       addonAfter={<Button onClick={async (e) => {
                                                           e.stopPropagation()
                                                           if (await event.user.password({...pwdForm.getFieldsValue(), ...{uid: record._id}})) {
                                                               message.success('')
                                                           } else {
                                                               message.error('')
                                                           }
                                                           pwdForm.resetFields()
                                                       }}></Button>}/>
                                            </Form.Item>
                                        </Form>
                                    }}
                                >
                                    <Button onClick={(e) => e.stopPropagation()}></Button>

                                </Popover>
                            </Space>
                        </>
                    )
                } else {
                    return <Button danger disabled></Button>
                }

            }
        }
    ]
    return (
        <>
            <Row justify="end">
                <Col span={2}>
                    <EditSettingUser show={showModal} setShow={setShowModal} initData={modalInit}
                                     setInitData={setModalInit}/>
                    <Button type={'primary'} onClick={async () => {
                        setShowModal(true)
                    }}></Button>
                </Col>
            </Row>
            <Table
                columns={columns}
                dataSource={state.setting.user.user}
                size={'small'}
                bordered
                onRow={(record) => {
                    return {
                        onClick: (event) => {
                            if (record.role === g.user_role.normal) {
                                setModalInit({
                                    ...{...record, ...{birth: moment(record.birth)}}
                                })
                                setShowModal(true)
                            } else {
                                message.info('')
                            }
                        }
                    }
                }}
            />
        </>
    )
}

const EditSettingUser = (props) => {
    const state = store.useContext()
    const [form] = Form.useForm()
    form.setFieldsValue(props.initData)
    return (
        <>
            <Modal key={Math.random()}
                   title={''}
                   visible={props.show}
                   okText={''} cancelText={''}
                   onCancel={(e) => {
                       props.setShow(false)
                       form.resetFields()
                   }}
                   onOk={() => {
                       form.validateFields()
                           .then(async (values) => {
                               values = {...values, ...{birth: values.birth.valueOf()}}
                               if (!await event.user.saveUser(values)) {
                                   message.error('')
                                   return
                               } else {
                                   message.success('')
                                   props.setShow(false)
                                   form.resetFields()
                               }
                           })
                   }}
            >
                <Form key={Math.random()} form={form}>
                    <Form.Item label={'_id'} name={'_id'} hidden={true}>
                        <Input/>
                    </Form.Item>
                    <Form.Item label={''} name={'username'} rules={[
                        {
                            required: true,
                            message: 'is required!',
                        }
                    ]}>
                        <Input/>
                    </Form.Item>

                    <Form.Item label={''} name={'birth'} rules={[
                        {
                            required: true,
                            message: 'is required!',
                        }
                    ]}>
                        <DatePicker/>
                    </Form.Item>
                    <Form.Item label={''} name={'email'} rules={[
                        {
                            required: true,
                            message: 'is required!',
                        }
                    ]}>
                        <Input type={'email'}/>
                    </Form.Item>
                    <Form.Item label={''} name={'personal_email'} rules={[
                        {
                            required: true,
                            message: 'is required!',
                        }
                    ]}>
                        <Input type={'email'}/>
                    </Form.Item>
                    <Form.Item label={''} name={'mobile'} rules={[
                        {
                            required: true,
                            message: 'is required!',
                        }
                    ]}>
                        <Input type={'tel'}/>
                    </Form.Item>
                    <Form.Item label={''} name={'address'} rules={[
                        {
                            required: true,
                            message: 'is required!',
                        }
                    ]}>
                        <Input/>
                    </Form.Item>
                    <Form.Item label={''} name={'urgent_name'} rules={[
                        {
                            required: true,
                            message: 'is required!',
                        }
                    ]}>
                        <Input/>
                    </Form.Item>
                    <Form.Item label={''} name={'urgent_mobile'} rules={[
                        {
                            required: true,
                            message: 'is required!',
                        }
                    ]}>
                        <Input type={'tel'}/>
                    </Form.Item>
                    <Form.Item noStyle shouldUpdate>
                        {
                            (ins) => {
                                return (
                                    <Form.Item label={''} name={'group_list'} rules={[
                                        {
                                            required: true,
                                            message: 'is required!',
                                        }
                                    ]}>
                                        <Select
                                            mode="multiple"
                                            allowClear
                                            placeholder=""
                                            optionFilterProp="children"
                                            filterOption={(input, option) =>
                                                option.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
                                            }
                                        >
                                            {state.setting.menu_group.menu_group.map((item) => <Select.Option
                                                key={item._id}
                                                value={item._id}>{item.name}</Select.Option>)}
                                        </Select>
                                    </Form.Item>
                                )
                            }
                        }
                    </Form.Item>
                </Form>
            </Modal>
        </>
    )
}
